.description {
  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    color: #444;
  }

  &__text:not(:first-child) {
    margin-top: 20px;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-top: 25px;
    gap: 30px;
  }

  &__table {
    border-spacing: 0 11px;
    width: 100%;
    max-width: 482px;
  }

  &__characteristic {
  }

  &__row {
  }

  &__header {
    text-align: start;
  }

  &__data {
    text-align: end;
  }

  &__header,
  &__data {
    border-bottom: 1px solid #ddd;
    font-weight: 400;
    font-size: 12px;
    line-height: 167%;
    color: #454343;
  }

  &__similar {
    margin-bottom: 16px;
  }
}

.characteristic,
.compound,
.storage {
  width: 100%;
  max-width: 482px;
}

