.where {
  &__wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;

    gap: 15px;

    @media (max-width: 650px) {
      flex-direction: column;
    }
  }

  &__left {
    max-width: 510px;
  }

  &__left-paragraph {
    font-weight: 400;
    font-size: 14px;
    line-height: 171%;
    color: #444;
  }

  &__left-paragraph:not(:first-child) {
    margin-top: 20px;
  }

  &__right {
    @media (max-width: 650px) {
      display: flex;
      flex-direction: column;
    }
  }

  &__span {
    margin-bottom: 17px;

    font-weight: 600;
    font-size: 15px;
    line-height: 133%;
    color: #444;
    @media (min-width: 651px) {
      display: none;
    }
  }

  &__image {
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.08);
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 25px;

    gap: 5px;

    @include mobile {
      margin-top: 34px;
    }
  }

  &__item {
  }

  &__button {
    border: 0.5px solid #444;
    border-radius: 5px;
    padding: 10px 40px;

    width: 100%;
    max-width: fit-content;

    font-weight: 500;
    font-size: 14px;
    line-height: 143%;
    text-align: center;
    color: #444;

    @include mobile {
      font-weight: 600;
      font-size: 15px;
      line-height: 133%;
      color: #043e44;
    }

    @media (max-width: 440px) {
      padding: 9px 25px;
    }

    @media (max-width: 375px) {
      padding: 9px 15px;

      font-size: 14px;
    }
  }
}
