.similar {
  &__title {
    margin: 0;
    margin-bottom: 30px;

    font-weight: 500;
    font-size: clamp(1.125rem, 1.0643rem + 0.2589vw, 1.375rem);
    line-height: 91%;
    color: #444;

    @include mobile {
      line-height: 111%;
    }
  }

  &__swiper {
  }

  &__article {
    position: relative;

    display: flex;
    flex-direction: column;
    border: 1px solid #dedede;
    border-radius: 10px;
    height: 100%;

    @media (max-width: 500px) {
      flex-direction: row;
      padding: 28px 21px 21px 2px;
    }
  }

  &__article-button-like {
    display: none;
    @media (min-width: 501px) {
      position: absolute;
      right: 16px;
      top: 16px;
      display: block;
    }
  }

  &__article-image-like {
  }

  &__article-image {
    border-radius: 9px 9px 0 0;

    @media (max-width: 500px) {
      border-radius: 9px;
      max-height: 94px;
    }
  }

  &__article-link {
  }

  &__article-wrapper {
    padding-left: 4px;
    width: 100%;

    @media (min-width: 501px) {
      padding: 14px 0 20px 12px;
    }
  }

  &__article-description {
    display: flex;
    flex-direction: column;
    gap: 7px;
  }

  &__article-title {
    margin: 0;

    font-weight: 600;
    font-size: clamp(0.9375rem, 0.9527rem + -0.0647vw, 0.875rem);
    line-height: 129%;
    color: #424141;

    @media (max-width: 500px) {
      line-height: 120%;
      color: #000;
    }
  }

  &__article-autor {
    font-weight: 400;
    font-size: clamp(0.6875rem, 0.6723rem + 0.0647vw, 0.75rem);
    line-height: 150%;
    color: #89817f;

    @media (max-width: 500px) {
      line-height: 164%;
      color: #000;
    }
  }

  &__down {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;

    gap: 5px;

    @media (max-width: 500px) {
      flex-direction: column;
      align-items: end;
      gap: 14px;
    }
  }

  &__article-price {
  }

  &__article-current-price {
    font-weight: 600;
    font-size: clamp(1.125rem, 1.0947rem + 0.1294vw, 1.25rem);
    line-height: 90%;
    color: #3d3c3c;

    @media (max-width: 500px) {
      font-weight: 700;

      line-height: 100%;
      color: #444;
    }
  }

  &__article-separator {
    font-weight: 400;
    font-size: clamp(0.625rem, 0.5947rem + 0.1294vw, 0.75rem);
    color: #a69895;

    @media (max-width: 500px) {
      font-weight: 600;
      color: #4d4b4b;
    }
  }

  &__article-capacity {
    font-weight: 400;
    font-size: clamp(0.8125rem, 0.8277rem + -0.0647vw, 0.75rem);
    color: #a69895;

    @media (max-width: 500px) {
      font-weight: 500;
      color: #4d4b4b;
    }
  }

  &__atricle-cart {
    border-radius: 20px 0 0 20px;
    padding: 5px 21px;
    width: 100%;
    max-width: 66px;
    max-height: 36px;

    background-color: var(--green-color);

    @media (max-width: 501px) {
      display: flex;
      align-items: center;
      border: 0.7px solid #dedede;
      border-radius: 5px;
      padding: 14px 15px;
      max-width: fit-content;
      max-height: 46px;

      font-weight: 600;
      font-size: 14px;
      line-height: 129%;
      color: #429f3a;
      background-color: #fff;
      gap: 19px;
    }
  }

  &__article-cart-wrapper {
    display: flex;
    align-items: center;
    gap: 5px;
  }

  &__article-icon-cart {
  }

  &__article-rating {
    margin-top: 1.5px;
  }

  &__list {
    display: flex;
    align-items: center;
    gap: 3px;
  }

  &__item {
  }

  &__article-icon-star {
  }
}

.swiper-slide {
  width: 250px;
  height: auto;
  @media (max-width: 500px) {
    width: 288px;
  }
}
