.review {
  &__paragraph {
    margin: 0;

    max-width: 525px;

    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    color: #444;
  }

  &__button {
    margin-top: 33px;
    border-radius: 5px;
    padding: 10px 18px;

    font-weight: 600;
    font-size: 15px;
    line-height: 133%;
    text-align: center;
    color: #fff;

    background-color: var(--green-color);

    @include mobile {
      display: flex;
      margin-right: auto;
      margin-left: auto;
    }
  }
}
