.offset {
  margin: 55px 0 0;

  @include mobile {
    margin: 24px 0 0;
  }
}

.pc-vision {
  @media (max-width: 500px) {
    display: none !important;
  }
}

.small-mobile-vision {
  @media (min-width: 501px) {
    display: none !important;
  }
}

.text-mobile {
  margin: 0;
  @include mobile {
    text-align: justify;
  }
}

.title-table {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: clamp(0.9375rem, 0.9223rem + 0.0647vw, 1rem);
  line-height: 125%;
  color: #444;
  @include mobile {
    font-weight: 600;
  }
}
