.header {
  margin-top: 16px;
  &__container {
  }

  &__tabs {
    border-bottom: 0.5px solid #b2b2b2;
  }
}

.tabs {
  &__nav {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 100px;

    @include mobile {
      gap: 45px;
    }

    @media (max-width: 370px) {
      gap: 20px;
    }
  }

  &__nav-item {
  }

  &__nav-btn {
    position: relative;
    padding-bottom: 20px;
    font-weight: 500;
    font-size: clamp(0.9375rem, 0.9223rem + 0.0647vw, 1rem);
    line-height: 125%;
    text-transform: uppercase;
    color: #444;

    @include mobile {
      line-height: 133%;
    }
  }

  &__nav-btn.active {
    font-weight: 600;

    @include mobile {
      font-weight: 700;
    }
  }

  &__nav-btn.active::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    border-radius: 5px;
    width: 100%;
    height: 5px;
    background-color: var(--green-color);
    transform: translate(-50%, -50%);
  }
}
